import React from 'react';
import { Col, FormGroup, Label, Input } from 'reactstrap';
import './CoffeeSelect.css';


function CoffeeSelect ({changedMenu}){
    return (
        <FormGroup row>
        <Col xs={1}/>
        <Col xs={3}>
            <Label for="coffeeSelect">커피 :</Label>
        </Col>
        <Col xs={7}>
            <Input type="select" name="select" id="coffeeSelect"  onChange={changedMenu}>
                <option value="">커피 선택</option>
                <option>아메리카노</option>
                <option>라떼</option>
                <option>자스민티</option>
                <option>토마토주스</option>
                <option>카페모카</option>
            </Input>
        </Col>
        </FormGroup>
    );
}

export default CoffeeSelect;



